import { useEffect, useRef, useState } from 'react'

export const useAudio = (filename) => {
  const clip = useRef(new Audio(`/api/audio/${filename}`))
  const [playing, setPlaying] = useState(false)
  const [progress, setProgress] = useState(0)
  const [complete, setComplete] = useState(false)
  const togglePlay = () => {
    // TODO: One day figure out how web audio on iOS works. this makes it seem like it'll work as long as play and pause are programmatically called as part of a user action, not as a result of a change in value...
    if (playing) {
      clip.current.pause()
      setPlaying(false)
      return
    }
    clip.current.play()
    setPlaying(true)
  }
  const replay = () => {
    clip.current.currentTime = 0
    setPlaying(true)
    setProgress(0)
    setComplete(false)
    clip.current.play()
  }
  useEffect(() => {
    if (playing) {
      let interval = setInterval(() => {
        let { currentTime, duration } = clip.current
        if (currentTime === duration) {
          setComplete(true)
          setPlaying(false)
          clearInterval(interval)
        }
        let percent = Math.floor(100 * (currentTime / duration))
        setProgress(percent)
      }, 100)
      return () => clearInterval(interval)
    }
    return
  }, [playing])
  return { progress, playing, complete, togglePlay, replay }
}
