import React from 'react'
import { useParams } from 'react-router'
import styled from 'styled-components'
import { useStop } from '../useData'
import Loading from '../Loading'
import Player from './Player'
import { Card } from '../Components'
import Error from '../Error'
const Text = styled.div`
  font-family: sans-serif;
  margin: 15px;
  h1 {
    font-size: 2.25em;
    margin-top: 15px;
    margin-bottom: 12px;
  }
  p {
    margin-bottom: 1em;
    line-height: 1.4em;
  }
`

const Stop = () => {
  const { slug } = useParams()
  const { stop, loading } = useStop(slug)
  if (loading) {
    return <Loading />
  }
  if (stop == null) {
    return <Error>ERROR: Can't find that stop</Error>
  }
  return (
    <Card>
      <Player data={stop} />
      <Text>
        <h1>{stop.title}</h1>
        {stop.description.map((entry, i) => (
          <p key='i'>{entry}</p>
        ))}
      </Text>
    </Card>
  )
}

export default Stop
