import React from 'react'
import styled from 'styled-components'
import Play from '@material-ui/icons/PlayArrow'
import Pause from '@material-ui/icons/Pause'
import Replay from '@material-ui/icons/Replay'
const Container = styled.div`
  display: grid;
  grid-template-columns: 1fr 2fr 3fr 2fr 1fr;
  place-items: center;
`
const Button = styled.button`
  transition: all 0.2s ease;
  background-color: ${({ theme, color = 'blue' }) => theme[color]};
  padding: 15px;
  border-radius: 50%;
  border: none;
  grid-column: 3/4;
  grid-row: 1/2;
`
const SideButton = styled(Button)`
  grid-column: 1/4;
  padding: 9px;
  background-color: ${({ theme, color = 'yellow' }) => theme[color]};
`
const Buttons = ({ playing, togglePlay, complete, replay, progress }) => {
  return (
    <Container>
      {progress > 0 && progress < 100 ? (
        <SideButton onClick={replay}>
          <Replay />
        </SideButton>
      ) : null}
      {complete ? (
        <Button onClick={replay} color='yellow'>
          <Replay />
        </Button>
      ) : playing ? (
        <Button onClick={togglePlay}>
          <Pause />
        </Button>
      ) : (
        <Button onClick={togglePlay}>
          <Play />
        </Button>
      )}
    </Container>
  )
}
export default Buttons
