import React from 'react'
import { useParams } from 'react-router'
import { useStop } from '../useData'
import { Card } from '../Components'
import styled from 'styled-components'

const QRCard = styled(Card)`
  place-items: center center;
  padding: 25px;
`
const Button = styled.button`
  background-color: ${(props) => props.theme.yellow};
  border: none;
  border-radius: 2px;
  padding: 15px;
  &:hover {
    box-shadow: 1px 1px 1px #eee;
    cursor: pointer;
  }
`
const QR = () => {
  const { slug } = useParams()
  const location = window.location.href.toLowerCase().split('/qr')[0]
  const qrReq = `/api/qr/256/${encodeURIComponent(location)}.svg`
  const { loading, stop } = useStop(slug)
  if (loading) {
    return <h1>Loading..</h1>
  }
  if (stop == null) {
    return <h2>Error</h2>
  }
  return (
    <QRCard>
      <h1>{stop.title} QR CODE</h1>
      <img src={qrReq} alt={`QR Code for ${location}`} />

      <a href={qrReq} download={`${stop.slug}.svg`}>
        <Button>Click to download QR for: {location}</Button>
      </a>
    </QRCard>
  )
}

export default QR
